import { CountDown } from "../../components/countDown";
import { state } from "../../state";

export function initGamePage(conteiner) {
  const divEl = document.querySelector(".root");

  divEl.innerHTML = `
    <count-down class="timer"></count-down>
    <div class="options"> 
    <options-comp></options-comp> 
  `;

  const options = divEl.querySelector(".options").children;
  for (const option of options) {
    option.addEventListener("optionEvent", (e: any) => {
      const typeSelected = e.detail.type;
      state.setMove(typeSelected);
      const typeComputer = state.computerMove();
      divEl.innerHTML = `
      <my-text tag="h2">Cargando los resultados</my-text>
      <div class="plays">
      <my-text class="modifier" tag="p">Tu jugada: ${typeSelected}</my-text>
      <my-text class="modifier" tag="p">La compu: ${typeComputer}</my-text>
      </div>
        `;

      divEl.appendChild(style);

      setTimeout(() => {
        state.saveData();
        conteiner.goTo("/piedra-papel-o-tijera/results");
      }, 3000);
    });
  }

  const style = document.createElement("style");
  style.innerText = `
  *{
    box-sizing: border-box;
  }
  .root{
    display: flex;
    flex-direction: column; 
    align-items: center;
    justify-content: space-around;
    min-width: 375px;
    min-height: 731px;
    background-color: pink;
  }
  .plays{
      display: flex;
      flex-direction: column;
      width: 259px;
      height: 217px;
      padding: 15px 30px;
      background-color: #FFFFFF;
      border: 10px solid #000000;
      border-radius: 10px;
  }
 
  `;

  divEl.appendChild(style);

  return divEl;
}
