import { state } from "../../state";

export function initResultsPage(conteiner) {
  const divEl = document.querySelector(".root");

  const style = document.createElement("style");
  style.innerText = `
    .root{
      display: flex;
      flex-direction: column; 
      align-items: center;
      justify-content: space-around;
      min-width: 375px;
      min-height: 731px;
      background-color: pink;
    }
    .score-conteiner{
      display: flex;
      flex-direction: column;
      width: 259px;
      height: 217px;
      padding: 15px 30px;
      background-color: #FFFFFF;
      border: 10px solid #000000;
      border-radius: 10px;
    }
    `;

  let result;
  if (state.whoWins()) {
    result = "win";
  } else {
    result = "loose";
  }
  console.log(result);
  divEl.innerHTML = `
    <star-comp tag=${result}></star-comp>
    <div class="score-conteiner"> 
    <my-text tag="h2">Puntaje</my-text>
    <my-text tag="p" id="your-scrore">Vos:${state.getScore().win}</my-text>
    <my-text tag="p" id="computer-scrore">Máquina:${
      state.getScore().loose
    }</my-text>
    </div>
    <my-button class="re-starButton">Volver a jugar</my-button>
    <my-button class="clean-data">Borrar historial</my-button>
    `;

  const buttonReStartEl = divEl.querySelector(".re-starButton");
  buttonReStartEl.addEventListener("click", (e) => {
    state.getState();
    conteiner.goTo("/piedra-papel-o-tijera/game");
  });
  const buttonCleanDataEl = divEl.querySelector(".clean-data");
  buttonCleanDataEl.addEventListener("click", () => {
    console.log("Hola soy clean data");
    state.cleanData();
    console.log(state.getState());
    conteiner.goTo("/piedra-papel-o-tijera/welcome");
  });

  divEl.appendChild(style);
  state.saveData();
  return divEl;
}
