import { state } from "../../state";

export class Star extends HTMLElement {
  shadow: ShadowRoot;
  tag: string;
  constructor() {
    super();
    const shadow = this.attachShadow({ mode: "open" });
    this.tag = this.getAttribute("tag");
    this.render();
  }
  render() {
    const startWinURL = require("url:../../img/starWin.svg");
    const startLoseURL = require("url:../../img/starLose.svg");
    const divEl = document.createElement("div");
    console.log(this.tag);
    divEl.className = "root";
    if (this.tag == "win") {
      divEl.innerHTML = `
      <img class="star"  src=${startWinURL}>
      <my-text tag="h3" class="centered">Ganaste</my-text>
      </img>`;
    } else {
      divEl.innerHTML = `
      <img class="star"  src=${startLoseURL}>
      <my-text tag="h3" class="centered">Perdiste</my-text>
      </img>`;
    }

    const style = document.createElement("style");
    style.innerText = `
      .root{
        text-align: center;
        position: relative;
      }
      .star{
        width: 259px;
        height: 259px;
      }
      .centered {
        position: absolute;
        top: 48%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
          `;

    this.shadow.appendChild(divEl);
    divEl.appendChild(style);
  }
}
customElements.define("star-comp", Star);
